.App {
  text-align: center;
}
* {
  border-collapse: collapse;
  margin:0rem;
  padding:0rem;
  text-decoration: none;
  color: black;
  list-style: none;
  font-family: Arial, Helvetica, sans-serif;
  box-sizing: inherit;
}
body {
  margin: 0;
}
.web-container {
  display: grid;
  grid-template-columns: 10rem auto;
  /* gap: 1rem; */
  height: 100vh;
  width: 100vw;
}
